"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import ProductForm from "@/components/shared/ProductForm";
import Sidebar from "@/components/shared/Sidebar";
import Loader from "@/components/shared/Loader";
import { uploadFiles, createProductAPI, fetchProductsAPI, formatProductData } from "@/utils/productApi";
import { showToast } from "@/components/shared/toast";

export default function AddProductPage() {
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [profile, setProfile] = useState(null);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(() => {
    if (typeof window !== "undefined") {
      const saved = localStorage.getItem("sidebarCollapsed");
      return saved !== null ? JSON.parse(saved) : false;
    }
    return false;
  });

  useEffect(() => {
    const userData = localStorage.getItem("user");
    if (userData) {
      try {
        const parsedUser = JSON.parse(userData);
        setProfile(parsedUser);
      } catch (err) {
        // ignore
      }
    }
  }, []);

  const handleSubmit = async ({ action, formData, imageFiles }) => {
    setLoading(true);
    try {
      let uploadedImages = [];
      if (imageFiles.length > 0) {
        uploadedImages = await uploadFiles(imageFiles);
      }
      const imageIds = uploadedImages.map((img) => img.id);
      const payload = {
        name: formData.name.trim(),
        description: formData.description.trim(),
        about_item: formData.about_item,
        product_details: formData.product_details,
        safety_information: formData.safety_information,
        usage_directions: formData.usage_directions,
        images: imageIds,
        status: action
      };
      console.log("Payload for new product:", payload);
      const data = await createProductAPI(payload);
      if (!data.success) throw new Error(data?.error || "Failed to create product");
      const message = action === "published" ? "Product published successfully!" : "Product saved as draft!";
      showToast(message, "success");
      // Dispatch custom event so Product.js can update instantly
      if (data.product) {
        const [formattedProduct] = formatProductData({ products: [data.product] });
        window.dispatchEvent(new CustomEvent("product-updated", { detail: { product: formattedProduct } }));
      }
      router.push("/products");
    } catch (err) {
      showToast(err.message || "Failed to create product", "error");
    } finally {
      setLoading(false);
    }
  };

  const handleCancel = () => {
    router.push("/products");
  };

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center">
      {/* LOADER */}
      <Loader 
        isVisible={loading} 
        message="Creating product..."
      />
      
      {/* SIDEBAR */}
      <Sidebar 
        profile={profile} 
        onLogout={() => { localStorage.removeItem("token"); router.push("/signin"); }}
        onCollapseChange={setSidebarCollapsed}
      />
      {/* MAIN CONTENT */}
      <div 
        className="flex-1 transition-all duration-300" 
        style={{ marginLeft: sidebarCollapsed ? '80px' : '256px' }}
      >
        <div className="max-w-8xl mx-5 px-6 py-8">
          {/* FORM */}
          <div className="flex justify-center items-start">
            <div className="w-full max-w-full mx-auto">
              <ProductForm
                mode="add"
                onSubmit={handleSubmit}
                onCancel={handleCancel}
                isLoading={loading}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
