"use client";
import React, { useState } from "react";
import dynamic from "next/dynamic";
import { commands } from "@uiw/react-md-editor";
import { useRef } from "react";
import { useState as useLocalState } from "react";

// Dynamically import MDEditor to avoid SSR issues
const MDEditor = dynamic(() => import("@uiw/react-md-editor"), { ssr: false });
import Image from "next/image";
import { STRAPI_URL } from "@/utils/apiConfig";


/**
 * ============================================================================
 * ProductForm Component - Unified Reusable Form for Add and Edit Product
 * ============================================================================
 * 
 * PURPOSE:
 * This is a shared component that handles both adding new products and editing 
 * existing products. It manages form inputs, image upload/carousel, and 
 * communicates with parent components through callback functions.
 * 
 * HOW IT CONNECTS WITH ADD PRODUCT:
 * - Used in: app/add-product/page.js
 * - Mode: 'add'
 * - Initial Data: Empty (no existing product data)
 * - Image Handling: Only new images uploaded by user
 * - On Submit: Sends formData + imageFiles to create new product in Strapi
 * 
 * HOW IT CONNECTS WITH EDIT PRODUCT:
 * - Used in: components/editProduct/EditProduct.js
 * - Mode: 'edit'
 * - Initial Data: Contains existing product (name, description, images)
 * - Image Handling: Shows existing Strapi images + allows new uploads
 * - On Submit: Sends formData + existingImages + new imageFiles to update product
 * 
 * DATA FLOW:
 * 1. Parent passes initialData (empty for add, populated for edit)
 * 2. Component manages state for form fields and images
 * 3. User interacts with form (edit fields, upload/delete images)
 * 4. On submit, component calls onSubmit callback with all data
 * 5. Parent handles API call to backend (create or update)
 * 
 * @param {Object} props
 * @param {Object} props.initialData - Initial form data for editing (optional)
 *        - For ADD mode: {} (empty object)
 *        - For EDIT mode: { name,  description, image: [...] }
 * @param {Function} props.onSubmit - Callback when form submitted
 *        - Receives: { formData, imageFiles, existingImages }
 * @param {Function} props.onCancel - Callback when cancel button clicked
 * @param {boolean} props.isLoading - Shows loading state on save button
 * @param {string} props.mode - 'add' or 'edit' determines UI and behavior
 */
export default function ProductForm({ 
  initialData = {}, 
  onSubmit, 
  onCancel, 
  isLoading = false,
  mode = 'add'
}) {
  const [submitAction, setSubmitAction] = useState(null);

  /**
   * formData: Stores text input values (name,  description)
   * - In ADD mode: Starts empty
   * - In EDIT mode: Pre-filled with existing product data
   */
  const [formData, setFormData] = useState({
    name: initialData.name || "",
    description: initialData.description || "",
    about_item: initialData.about_item || "",
    product_details: initialData.product_details || "",
    safety_information: initialData.safety_information || "",
    usage_directions: initialData.usage_directions || "",
  });
  const [errors, setErrors] = useState({});
  const DESCRIPTION_LIMIT = 500;

  /**
   * imageFiles: Array of File objects for newly uploaded images
   * - Used when user selects new images from their device
   * - Sent to backend for upload to Strapi
   */
  const [imageFiles, setImageFiles] = useState([]);
  const [imageTypeError, setImageTypeError] = useState("");
  const [imageSizeError, setImageSizeError] = useState("");
  const VIDEO_SIZE_LIMIT = 4 * 1024 * 1024; // 4MB
  
  /**
   * imagePreviews: Array of preview objects for newly uploaded images
   * - Structure: [{ url: blob URL, name: filename }, ...]
   * - Used to show preview before upload
   */
  const [imagePreviews, setImagePreviews] = useState([]);
  
  /**
   * currentImageIndex: Tracks which image is currently displayed in carousel
   */
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  
  /**
   * existingImages: Array of existing images from Strapi (EDIT mode only)
   * - Structure: [{ id, url, name }, ...]
   * - Starts with all images from product
   * - User can delete images, which removes them from this array
   * - On submit, only remaining images are kept in product
   */
  const [existingImages, setExistingImages] = useState(initialData.image || []);

  /**
   * selectedSlots: Array of indices indicating which existing image slots are selected for replacement
   * - Used in EDIT mode only
   * - When user uploads new images, selected slots will be replaced
   */
  const [selectedSlots, setSelectedSlots] = useState([]);

  /**
   * handleInputChange: Updates form data when user types in input fields
   */
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name === "description") {
      if (value.length <= DESCRIPTION_LIMIT) {
        setFormData(prev => ({ ...prev, [name]: value }));
      }
    } else {
      setFormData(prev => ({ ...prev, [name]: value }));
    }
  };

  /**
   * handleDeleteExistingImage: Deletes existing image from product
   * - Removes from existingImages array
   * - Adjusts selectedSlots indices
   */
  const handleDeleteExistingImage = (index) => {
    const updated = existingImages.filter((_, i) => i !== index);
    setExistingImages(updated);

    // Adjust selected slots after deletion
    setSelectedSlots((prev) =>
      prev.filter((i) => i !== index).map((i) => (i > index ? i - 1 : i))
    );
  };

  /**
   * handleDeleteNewImage: Deletes newly uploaded image preview
   */
  const handleDeleteNewImage = (index) => {
    setImageFiles((prev) => prev.filter((_, i) => i !== index));
    setImagePreviews((prev) => prev.filter((_, i) => i !== index));
  };

  /**
   * handleSubmit: Called when user clicks Save button
   * - Prevents default form submission
   * - Calls parent's onSubmit with all form data
   * 
   * DATA SENT TO PARENT:
   * - formData: { name,  description }
   * - imageFiles: Array of new File objects to upload
   * - existingImages: Array of existing images to keep (after deletions)
   * - selectedSlots: Array of indices for slots to replace with new images
   */
  const handleSubmit = (e) => {
    if (e) e.preventDefault();
    // Validate required fields (ensure string type)
    const newErrors = {};
    const name = String(formData.name ?? '');
    const description = String(formData.description ?? '');
    const about_item = String(formData.about_item ?? '');
    const product_details = String(formData.product_details ?? '');
    const safety_information = String(formData.safety_information ?? '');
    const usage_directions = String(formData.usage_directions ?? '');
    // Images validation
    let imagesCount = 0;
    if (mode === 'add') {
      imagesCount = imageFiles.length;
    } else {
      imagesCount = (existingImages?.length || 0) + (imageFiles?.length || 0);
    }
    if (!name.trim()) newErrors.name = 'Name is required';
    if (!description.trim()) newErrors.description = 'Description is required';
    if (!about_item.trim()) newErrors.about_item = 'About Item is required';
    if (!product_details.trim()) newErrors.product_details = 'Product Details is required';
    if (!safety_information.trim()) newErrors.safety_information = 'Safety Information is required';
    if (!usage_directions.trim()) newErrors.usage_directions = 'Usage Directions is required';
    if (imagesCount === 0) newErrors.images = 'At least one image is required';
    setErrors(newErrors);
    if (Object.keys(newErrors).length > 0) return;
    if (submitAction) {
      onSubmit({
        action: submitAction,
        formData: { ...formData, name, description, about_item, product_details, safety_information, usage_directions },
        imageFiles,
        existingImages,
        selectedSlots
      });
      setSubmitAction(null);
    }
  };

  /**
   * handleSubmit: Called when user clicks Save button
   */
  const handleImageSelect = (e) => {
    setImageTypeError("");
    setImageSizeError("");
    const files = Array.from(e.target.files || []);
    const allowedImageTypes = ["image/png", "image/jpeg", "image/jpg"];
    const allowedVideoTypes = ["video/mp4", "video/webm", "video/ogg"];
    const allowedTypes = [...allowedImageTypes, ...allowedVideoTypes];
    
    const invalid = files.find(f => !allowedTypes.includes(f.type));
    if (invalid) {
      setImageTypeError("Only PNG, JPEG images and MP4, WebM, OGG videos are allowed.");
      e.target.value = '';
      return;
    }
    
    // Check video file size (4MB limit)
    const oversizedVideo = files.find(f => {
      if (f.type.startsWith('video/') && f.size > VIDEO_SIZE_LIMIT) {
        return true;
      }
      return false;
    });
    
    if (oversizedVideo) {
      setImageSizeError(`Video file "${oversizedVideo.name}" exceeds 4MB size limit`);
      e.target.value = '';
      return;
    }
    
    setImageFiles(files);
    const previews = files.map(file => ({
      url: URL.createObjectURL(file),
      name: file.name,
      type: file.type.startsWith('video/') ? 'video' : 'image'
    }));
    setImagePreviews(previews);
  };

  /**
   * Generate preview images with new uploads replacing selected slots
   * - EDIT mode: Shows existing images with replacements in selected slots
   * - ADD mode: Just shows the uploaded images
   */
  const getDisplayImages = () => {
    // Helper function to detect if a URL/file is a video
    const isVideoFile = (item) => {
      if (item.type === 'video') return true;
      const url = item.url || '';
      return /\.(mp4|webm|ogg|mov|avi|mkv)$/i.test(url);
    };

    if (mode === 'edit' && existingImages.length > 0) {
      // Create a copy of existing images, add type property for existing files
      const preview = [...existingImages].map(img => ({
        ...img,
        type: img.type || (isVideoFile(img) ? 'video' : 'image')
      }));
      
      // Replace selected slots with new uploads
      selectedSlots.forEach((slotIndex, i) => {
        if (i < imagePreviews.length) {
          preview[slotIndex] = {
            ...imagePreviews[i],
            isNew: true // Mark as new for different display
          };
        }
      });
      
      // Append remaining new images
      if (imagePreviews.length > selectedSlots.length) {
        preview.push(...imagePreviews.slice(selectedSlots.length).map(img => ({
          ...img,
          isNew: true
        })));
      }
      
      return preview;
    }
    // ADD mode: just return imagePreviews (newly uploaded files)
    return imagePreviews;
  };

  const displayImages = getDisplayImages();

  /**
   * nextImage: Navigate to next image in carousel
   */
  const nextImage = () => {
    setCurrentImageIndex((prev) => (prev + 1) % displayImages.length);
  };

  /**
   * prevImage: Navigate to previous image in carousel
   */
  const prevImage = () => {
    setCurrentImageIndex((prev) => (prev - 1 + displayImages.length) % displayImages.length);
  };

  /**
   * handleRemoveImage: Delete current image from carousel
   */
  const handleRemoveImage = (index) => {
    const displayImages = getDisplayImages();
    
    if (mode === 'edit') {
      const item = displayImages[index];
      
      // If it's a new item (marked with isNew flag)
      if (item?.isNew) {
        // Find which preview this corresponds to and remove it
        const previewIndex = imagePreviews.findIndex(prev => 
          prev.url === item.url && prev.name === item.name
        );
        if (previewIndex !== -1) {
          handleDeleteNewImage(previewIndex);
          // Remove from selectedSlots if it was a replacement
          setSelectedSlots(prev => prev.filter(slot => slot !== index));
        }
      } else {
        // It's an existing image from the backend
        const existingIndex = existingImages.findIndex(img => 
          img.url === item.url || img.id === item.id
        );
        if (existingIndex !== -1) {
          handleDeleteExistingImage(existingIndex);
        }
      }
    } else {
      // ADD mode: just delete from imagePreviews
      handleDeleteNewImage(index);
    }
    
    // Adjust current index if necessary
    const updatedDisplay = getDisplayImages();
    if (currentImageIndex >= updatedDisplay.length && updatedDisplay.length > 0) {
      setCurrentImageIndex(updatedDisplay.length - 1);
    } else if (updatedDisplay.length === 0) {
      setCurrentImageIndex(0);
    }
  };

  
  // Ref for hidden file input for image upload in markdown
  const mdImageInputRef = useRef(null);

  // Insert image markdown at cursor position
  const insertImageMarkdown = (file) => {
    if (!file) return;
    const url = URL.createObjectURL(file);
    // Insert at cursor (append if not possible)
    setFormData((prev) => {
      const desc = prev.description || "";
      const md = `![${file.name}](${url})`;
      return { ...prev, description: desc + (desc && !desc.endsWith('\n') ? '\n' : '') + md + '\n' };
    });
  };

  // Custom image command for MDEditor
  const customImageCommand = {
    ...commands.image,
    execute: (state, api) => {
      if (mdImageInputRef.current) {
        mdImageInputRef.current.value = '';
        mdImageInputRef.current.click();
      }
    },
  };

  return (
    <div className="bg-white rounded-lg shadow-lg p-10 w-full max-w-4xl mx-auto font-sans">
      
      {/* ========== HEADER SECTION (MODE-SPECIFIC) ========== */}
      
      {/* ADD MODE: Simple title */}
      {mode === 'add' && (
        <div className="mb-4">
          <h1 className="text-xl font-bold text-gray-800">Add New Product</h1>
          <p className="text-gray-600 text-xs mt-1">Create a new product for your catalog</p>
        </div>
      )}
      {/* End of description section */}
        
      {/* EDIT MODE: Title with close button */}
      {mode === 'edit' && (
        
        <div className="flex justify-between items-start mb-6">
          <div>
            <h1 className="text-2xl font-bold text-gray-800">Edit Product</h1>
            <p className="text-gray-600 text-sm mt-1">Update your product catalog</p>
          </div>
          {/* Close button - calls onCancel callback */}
          <button
            type="button"
            onClick={onCancel}
            className="text-gray-500 hover:text-gray-700 text-xl"
          >
            âœ•
          </button>
        </div>
      )}
        
      <form onSubmit={handleSubmit}>
        
        {/* Name - full width */}
        <div className="mb-4">
          <label className="block text-xs font-medium text-black mb-1">
            Name <span className="text-red-500">*</span>
          </label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleInputChange}
            className={`w-full px-3 py-1.5 text-sm border ${errors.name ? 'border-red-500 focus:ring-red-500' : 'border-gray-300 focus:ring-green-500'} rounded-md focus:ring-2 focus:border-transparent`}
            placeholder="Enter product name"
            autoComplete="off"
          />
          {errors.name && <div className="text-xs text-red-500 mt-1">{errors.name}</div>}
        </div>

        {/* Description - full width with Markdown editor, toolbar, preview, and expand */}
        <div className="mb-4 w-full">
          <label className="block text-xs font-medium text-black mb-1">
            Description <span className="text-red-500">*</span>
          </label>
          <div className="w-full relative">
            {/* Preview mode button at top right */}
            <button
              type="button"
              className="absolute right-2 md-preview-mode-btn z-10 text-xs px-4 py-1 rounded border border-gray-700 bg-[#181a1b] text-white hover:bg-gray-900 shadow"
              onClick={() => setFormData((prev) => ({ ...prev, previewMode: !prev.previewMode }))}
              style={{ minWidth: 110 }}
            >
              {formData.previewMode ? 'Edit mode' : 'Preview mode'}
            </button>
            <div data-color-mode="dark">
              <MDEditor
                value={formData.description}
                onChange={(val) => {
                  if ((val || "").length <= DESCRIPTION_LIMIT) {
                    setFormData((prev) => ({ ...prev, description: val || "" }));
                  }
                }}
                height={300}
                preview={formData.previewMode ? "preview" : "edit"}
                commands={[
                  commands.group([
                    commands.title1,
                    commands.title2,
                    commands.title3,
                    commands.title4,
                    commands.title5,
                    commands.title6,
                  ], {
                    name: 'title',
                    groupName: 'title',
                    buttonProps: {
                      'aria-label': 'Headings',
                      title: 'Headings',
                      children: 'Headings',
                    },
                  }),
                  commands.bold,
                  commands.italic,
                  commands.underline,
                  commands.strikethrough,
                  commands.unorderedListCommand,
                  commands.orderedListCommand,
                  commands.code,
                  customImageCommand,
                  commands.link,
                  commands.quote,
                ]}
                extraCommands={[]}
                textareaProps={{
                  placeholder: "Enter description (supports Markdown and images)",
                  maxLength: DESCRIPTION_LIMIT,
                  name: "description",
                  style: { minHeight: 120, resize: 'vertical', background: '#fff', color: '#222' }
                }}
                style={{ width: '100%', borderRadius: 8, boxShadow: 'none', background: '#fff', color: '#222', border: '1px solid #000' }}
                visibleDragbar={false}
              />
            </div>
            {/* Hidden file input for markdown image upload */}
            <input
              type="file"
              accept="image/*"
              ref={mdImageInputRef}
              style={{ display: 'none' }}
              onChange={e => {
                const file = e.target.files && e.target.files[0];
                if (file) insertImageMarkdown(file);
              }}
            />
            </div>
            {/* Expand icon at bottom right */}
            <button
              type="button"
              className="absolute right-2 bottom-2 z-10 text-xs px-2 py-1 rounded border border-gray-300 bg-white text-gray-800 hover:bg-gray-100 shadow flex items-center"
              title="Expand"
              onClick={() => {
                const el = document.querySelector('.w-md-editor');
                if (el) el.classList.toggle('w-md-editor-fullscreen');
              }}
            >
              <svg width="18" height="18" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M4 8V4h4M20 8V4h-4M4 16v4h4M20 16v4h-4"/></svg>
              <span className="ml-1">Expand</span>
            </button>
          <div className="flex justify-between items-center mt-1">
            {errors.description && <div className="text-xs text-red-500">{errors.description}</div>}
            <div className="text-right text-xs text-gray-500 ml-auto">
              {formData.description.length} / {DESCRIPTION_LIMIT} characters
            </div>
          </div>
        </div>

        {/* Two-column grid for about_item/product_details and safety_info/usage_direction */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {/* About This Item */}
          <div>
            <label className="block text-xs font-medium text-black mb-1">
              About Item <span className="text-red-500">*</span>
            </label>
            <textarea
              name="about_item"
              value={formData.about_item}
              onChange={handleInputChange}
              className={`w-full px-3 py-1.5 text-sm border ${errors.about_item ? 'border-red-500 focus:ring-red-500' : 'border-gray-300 focus:ring-green-500'} rounded-md focus:ring-2 focus:border-transparent resize-none`}
              placeholder="Enter about item details"
              rows="3"
            />
            {errors.about_item && <div className="text-xs text-red-500 mt-1">{errors.about_item}</div>}
          </div>
          {/* Product Details */}
          <div>
            <label className="block text-xs font-medium text-black mb-1">
              Product Details <span className="text-red-500">*</span>
            </label>
            <textarea
              name="product_details"
              value={formData.product_details}
              onChange={handleInputChange}
              className={`w-full px-3 py-1.5 text-sm border ${errors.product_details ? 'border-red-500 focus:ring-red-500' : 'border-gray-300 focus:ring-green-500'} rounded-md focus:ring-2 focus:border-transparent resize-none`}
              placeholder="Enter product details"
              rows="3"
            />
            {errors.product_details && <div className="text-xs text-red-500 mt-1">{errors.product_details}</div>}
          </div>
          {/* Safety Information */}
          <div>
            <label className="block text-xs font-medium text-black mb-1">
              Safety Information <span className="text-red-500">*</span>
            </label>
            <textarea
              name="safety_information"
              value={formData.safety_information}
              onChange={handleInputChange}
              className={`w-full px-3 py-1.5 text-sm border ${errors.safety_information ? 'border-red-500 focus:ring-red-500' : 'border-gray-300 focus:ring-green-500'} rounded-md focus:ring-2 focus:border-transparent resize-none`}
              placeholder="Enter safety information"
              rows="3"
            />
            {errors.safety_information && <div className="text-xs text-red-500 mt-1">{errors.safety_information}</div>}
          </div>
          {/* Usage Directions */}
          <div>
            <label className="block text-xs font-medium text-black mb-1">
              Usage Directions <span className="text-red-500">*</span>
            </label>
            <textarea
              name="usage_directions"
              value={formData.usage_directions}
              onChange={handleInputChange}
              className={`w-full px-3 py-1.5 text-sm border ${errors.usage_directions ? 'border-red-500 focus:ring-red-500' : 'border-gray-300 focus:ring-green-500'} rounded-md focus:ring-2 focus:border-transparent resize-none`}
              placeholder="Enter usage directions"
              rows="3"
            />
            {errors.usage_directions && <div className="text-xs text-red-500 mt-1">{errors.usage_directions}</div>}
          </div>
        </div>

        {/* ========== IMAGE UPLOAD SECTION ========== */}
        <div className="mb-4">
          <label className="block text-xs font-medium text-black mb-1">
            Images <span className="text-red-500">*</span> { displayImages.length === 0 
      ? "" 
      : `[${currentImageIndex + 1}/${displayImages.length}]` 
  }
          </label>
          
          {errors.images && (
            <div className="text-xs text-red-500 mb-1">{errors.images}</div>
          )}
          {imageTypeError && (
            <div className="text-xs text-red-500 mb-1">{imageTypeError}</div>
          )}
          {imageSizeError && (
            <div className="flex items-center gap-2 bg-red-50 border border-red-200 rounded-md p-3 mb-3">
              <Image 
                src="/AG - error.png" 
                alt="Error" 
                width={40} 
                height={40}
                className="flex-shrink-0"
                unoptimized
              />
              <span className="text-sm text-red-600">{imageSizeError}</span>
            </div>
          )}
          {displayImages.length > 0 ? (
            <div className="relative bg-gray-100 rounded-lg p-3 mb-3 h-[160px]">
              
              {/* Previous Button */}
              {displayImages.length > 1 && (
                <button
                  type="button"
                  onClick={prevImage}
                  className="absolute left-2 top-1/2 -translate-y-1/2 bg-white rounded-full p-2 shadow-md hover:bg-gray-50 z-10 flex items-center justify-center"
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                  </svg>
                </button>
              )}

              {/* Image/Video Display */}
              <div className="h-24 flex items-center justify-center relative">
                {displayImages[currentImageIndex]?.type === 'video' ? (
                  <video
                    src={
                      displayImages[currentImageIndex]?.isNew || !displayImages[currentImageIndex]?.url?.startsWith('/')
                        ? displayImages[currentImageIndex]?.url || ""
                        : `${STRAPI_URL}${displayImages[currentImageIndex].url}`
                    }
                    className="max-h-24 max-w-full object-contain"
                    controls
                  />
                ) : (
                  <Image
                    src={
                      displayImages[currentImageIndex]?.isNew || !displayImages[currentImageIndex]?.url?.startsWith('/')
                        ? displayImages[currentImageIndex]?.url || ""
                        : `${STRAPI_URL}${displayImages[currentImageIndex].url}`
                    }
                    alt="Product preview"
                    width={300}
                    height={96}
                    className="max-h-24 max-w-full object-contain"
                    unoptimized
                  />
                )}
              </div>

              {/* Action Controls - Bottom Center */}
              <div className="absolute bottom-3 left-0 right-0 flex items-center justify-center gap-2 px-3">
                
                {/* Image Name Display */}
                <div className="bg-white px-2 py-0.5 rounded-md shadow-sm max-w-xs truncate">
                  <span className="text-xs text-gray-700">
                    {displayImages[currentImageIndex]?.name || `Image ${currentImageIndex + 1}`}
                  </span>
                </div>

                <div className="flex items-center gap-0.5 bg-white rounded-md shadow-sm p-0.5">
                  
                  {/* Delete Icon */}
                  <button
                    type="button"
                    onClick={() => handleRemoveImage(currentImageIndex)}
                    className="hover:bg-gray-100 p-1 rounded cursor-pointer"
                    title="Delete image"
                  >
                    <svg className="w-3.5 h-3.5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                    </svg>
                  </button>

                  {/* Replace Icon */}
                  <label className="cursor-pointer hover:bg-gray-100 p-1 rounded" title="Replace current media">
                    <svg className="w-3.5 h-3.5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                    </svg>
                    <input
                      type="file"
                      accept="image/png,image/jpeg,image/jpg,video/mp4,video/webm,video/ogg"
                      onChange={(e) => {
                        setImageTypeError("");
                        setImageSizeError("");
                        const file = e.target.files?.[0];
                        const allowedImageTypes = ["image/png", "image/jpeg", "image/jpg"];
                        const allowedVideoTypes = ["video/mp4", "video/webm", "video/ogg"];
                        const allowedTypes = [...allowedImageTypes, ...allowedVideoTypes];
                        if (file && !allowedTypes.includes(file.type)) {
                          setImageTypeError("Only PNG, JPEG images and MP4, WebM, OGG videos are allowed.");
                          e.target.value = '';
                          return;
                        }
                        // Check video file size (4MB limit)
                        if (file && file.type.startsWith('video/') && file.size > VIDEO_SIZE_LIMIT) {
                          setImageSizeError(`Video file "${file.name}" exceeds 4MB size limit`);
                          e.target.value = '';
                          return;
                        }
                        if (file) {
                          if (mode === 'edit') {
                            const displayImages = getDisplayImages();
                            const currentItem = displayImages[currentImageIndex];
                            
                            // If replacing an existing image
                            if (!currentItem?.isNew) {
                              const existingIndex = existingImages.findIndex(img => 
                                img.url === currentItem.url || img.id === currentItem.id
                              );
                              if (existingIndex !== -1 && !selectedSlots.includes(existingIndex)) {
                                setSelectedSlots(prev => [...prev, existingIndex]);
                              }
                            }
                            
                            setImageFiles(prev => [...prev, file]);
                            setImagePreviews(prev => [...prev, {
                              url: URL.createObjectURL(file),
                              name: file.name,
                              type: file.type.startsWith('video/') ? 'video' : 'image'
                            }]);
                          } else {
                            const newFiles = [...imageFiles];
                            newFiles[currentImageIndex] = file;
                            setImageFiles(newFiles);
                            const newPreviews = [...imagePreviews];
                            newPreviews[currentImageIndex] = {
                              url: URL.createObjectURL(file),
                              name: file.name,
                              type: file.type.startsWith('video/') ? 'video' : 'image'
                            };
                            setImagePreviews(newPreviews);
                          }
                        }
                        e.target.value = '';
                      }}
                      className="hidden"
                    />
                  </label>

                  {/* Add More Images/Videos Icon */}
                  <label className="cursor-pointer hover:bg-gray-100 p-1 rounded" title="Add more images or videos">
                    <svg className="w-3.5 h-3.5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                    </svg>
                    <input
                      type="file"
                      accept="image/png,image/jpeg,image/jpg,video/mp4,video/webm,video/ogg"
                      multiple
                      onChange={(e) => {
                        setImageTypeError("");
                        setImageSizeError("");
                        const newFiles = Array.from(e.target.files || []);
                        const allowedImageTypes = ["image/png", "image/jpeg", "image/jpg"];
                        const allowedVideoTypes = ["video/mp4", "video/webm", "video/ogg"];
                        const allowedTypes = [...allowedImageTypes, ...allowedVideoTypes];
                        const invalid = newFiles.find(f => !allowedTypes.includes(f.type));
                        if (invalid) {
                          setImageTypeError("Only PNG, JPEG images and MP4, WebM, OGG videos are allowed.");
                          e.target.value = '';
                          return;
                        }
                        // Check video file size (4MB limit)
                        const oversizedVideo = newFiles.find(f => {
                          if (f.type.startsWith('video/') && f.size > VIDEO_SIZE_LIMIT) {
                            return true;
                          }
                          return false;
                        });
                        if (oversizedVideo) {
                          setImageSizeError(`Video file "${oversizedVideo.name}" exceeds 4MB size limit`);
                          e.target.value = '';
                          return;
                        }
                        const updatedFiles = [...imageFiles, ...newFiles];
                        setImageFiles(updatedFiles);
                        const newPreviews = newFiles.map(file => ({
                          url: URL.createObjectURL(file),
                          name: file.name,
                          type: file.type.startsWith('video/') ? 'video' : 'image'
                        }));
                        setImagePreviews([...imagePreviews, ...newPreviews]);
                      }}
                      className="hidden"
                    />
                  </label>
                </div>
              </div>

              {/* Next Button */}
              {displayImages.length > 1 && (
                <button
                  type="button"
                  onClick={nextImage}
                  className="absolute right-2 top-1/2 -translate-y-1/2 bg-white rounded-full p-2 shadow-md hover:bg-gray-50 z-10 flex items-center justify-center"
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </button>
              )}
            </div>
            
          ) : (
            
            <label className="cursor-pointer block mb-3">
              <div className="relative bg-gray-50 border-2 border-dashed border-gray-300 rounded-lg hover:bg-gray-100 hover:border-green-400 transition-all h-[160px]">
                <div className="absolute inset-0 flex flex-col items-center justify-center text-center p-4">
                  <div className="w-10 h-10 bg-green-600 rounded-full flex items-center justify-center mb-2">
                    <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                    </svg>
                  </div>
                  <p className="text-gray-600 text-xs">
                    Click to add Images or Videos
                  </p>
                  <p className="text-gray-500 text-xs mt-1">
                    PNG, JPEG, MP4, WebM, OGG
                  </p>
                </div>
              </div>
              <input
                type="file"
                accept="image/png,image/jpeg,image/jpg,video/mp4,video/webm,video/ogg"
                multiple
                onChange={handleImageSelect}
                className="hidden"
              />
            </label>
          )}
        </div>

        {/* ========== FORM ACTION BUTTONS ========== */}
        <div className="flex justify-end gap-2 pt-2">
          {/* Cancel Button - calls onCancel callback */}
          <button
            type="button"
            onClick={onCancel}
            className="px-5 py-1.5 text-sm bg-green-600 hover:bg-green-700 rounded-md transition-colors text-white !text-white"
            style={{color: '#fff'}}
            disabled={isLoading}
          >
            Cancel
          </button>
          
          {/* Save/Submit Button - triggers handleSubmit */}
  {/* SAVE as Draft */}

  <button
    type="submit"
    onClick={() => setSubmitAction("draft")}
    className="bg-green-600 text-white px-4 py-2 rounded flex items-center justify-center"
    disabled={isLoading}
  >
    Save
  </button>

  {/* Publish */}

  <button
    type="submit"
    onClick={() => setSubmitAction("published")}
    className="bg-green-600 text-white px-4 py-2 rounded flex items-center justify-center"
    disabled={isLoading}
  >
    Publish
  </button>
        </div>
      </form>
    </div>
  );
}
